.wcf--filterable-gallery{

  .gallery-filter{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
    li{
      padding: 6px 10px;
      cursor: pointer;
    }
  }
  .gallery-wrapper{
    display: grid;
    grid-auto-flow: dense;
    gap: 15px;
  }
  .gallery-item{
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    .play__btn{
      position: absolute;
      left: 50%;
      top: 50%;
      font-size: 50px;
      transform: translate(-50%, -50%);
    }
  }

  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


  //masonry
  &.style-masonry .gallery-wrapper{
    grid-auto-rows: 250px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

    @media (min-width: 768px) {
      .horizontal {
        grid-column: span 2;
      }
      .vertical {
        grid-row: span 2;
      }
      .big {
        grid-column: span 2;
        grid-row: span 2;
      }
    }
  }

}
